<template>
  <div>
    <div v-if="slides.length == 0">
      <h1>Není vložen žádný snímek.</h1>
      <router-link to="/" class="btn">Návrat zpět</router-link>
    </div>
    <div v-else>
      <ToolBar :totalDuration="totalDuration" @setToAll="setToAll" />
      <div class="container">
        <div v-for="(slide, index) in slides" :key="index" class="preview">
          <div class="header">Snímek {{ index + 1 }}</div>
          <img :src="slides[index]" alt="" />
          <div class="footer">
            Doba trvání :
            <input
              type="number"
              v-model.number="durations[index]"
              @keyup.enter="
                $event.target.parentElement.parentElement.nextElementSibling.children[2].children[0].focus()
              "
            />
            min.
            <button class="btn-small" @click="increaseMin(index)" tabindex="-1">
              +
            </button>
            <button class="btn-small" @click="decreaseMin(index)" tabindex="-1">
              -
            </button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import ToolBar from '@/components/ToolBar';

export default {
  name: 'SlidesPreview',
  components: {
    ToolBar,
  },
  props: ['slides'],

  data() {
    return {
      durations: {},
    };
  },
  methods: {
    increaseMin(index) {
      this.durations[index] += 1;
    },
    decreaseMin(index) {
      if (this.durations[index] > 0) {
        this.durations[index] -= 1;
      }
    },
    setToAll(minutes) {
      this.slides.forEach((slide, index) => {
        console.log(`nastavuji index ${index}.`);
        this.$set(this.durations, index, minutes);
      });
    },
  },
  computed: {
    totalDuration() {
      return Object.values(this.durations).reduce((acc, time) => {
        acc += time;
        return acc;
      }, 0);
    },
  },
  watch: {
    totalDuration() {
      this.$emit('durationsChanged', this.totalDuration, this.durations);
    },
  },
};
</script>

